'use client';
import { useEffect, useState } from 'react';
import { Search } from 'lucide-react';
import Link from 'next/link';
import Image from 'next/image';
import { API_URL } from '../api/apiURL';

export default function UserTable() {
  const [users, setUsers] = useState([]);
  const [search, setSearch] = useState('');
  const [statusFilter, setStatusFilter] = useState('');
  const [genderFilter, setGenderFilter] = useState('');
  const [currentPage, setCurrentPage] = useState(1);
  const [totalPages, setTotalPages] = useState(1);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);

  const [sortField, setSortField] = useState('');
  const [sortOrder, setSortOrder] = useState('asc');

  const usersPerPage = 5;

  const handleSort = (field) => {
    if (sortField === field) {
      setSortOrder(sortOrder === 'asc' ? 'desc' : 'asc');
    } else {
      setSortField(field);
      setSortOrder('asc');
    }
  };

  const SortIcon = ({ field }) => (
    <span className="inline-flex flex-col ml-1 cursor-pointer">
      <span
        className={`leading-[6px] text-[10px] ${
          sortField === field && sortOrder === 'asc'
            ? 'text-black'
            : 'text-gray-400'
        }`}
      >
        ▲
      </span>
      <span
        className={`leading-[6px] text-[10px] ${
          sortField === field && sortOrder === 'desc'
            ? 'text-black'
            : 'text-gray-400'
        }`}
      >
        ▼
      </span>
    </span>
  );

  const fetchUsers = async () => {
    setLoading(true);
    setError(null);

    try {
      const params = new URLSearchParams({
        page: currentPage,
        limit: usersPerPage,
        search,
        statusFilter,
        genderFilter,
        sortField,
        sortOrder,
      });

      const res = await fetch(`${API_URL}/admin/user-manage?${params}`);
      const data = await res.json();

      if (data.success) {
        setUsers(data.data);
        setTotalPages(data.totalPages);
      } else {
        setError(data.message);
      }
    } catch (err) {
      setError(err.message || 'Failed to fetch users.');
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    fetchUsers();
  }, [currentPage, search, statusFilter, genderFilter, sortField, sortOrder]);

  const goToPage = (p) => {
    if (p >= 1 && p <= totalPages) setCurrentPage(p);
  };

  return (
    <div className="overflow-x-auto bg-white border border-gray-300 shadow-md rounded-xl p-4 mt-4 w-full">
      
      {/* SEARCH + FILTERS */}
      <div className="flex flex-col md:flex-row md:items-center border p-1 rounded border-gray-400 justify-between gap-3 mb-4">
        
        {/* SEARCH BAR */}
        <div className="relative w-full md:w-1/3">
          <input
            type="text"
            placeholder="Search By User"
            value={search}
            onChange={(e) => setSearch(e.target.value)}
            className="w-full pl-10 pr-4 py-2 border border-gray-400 rounded-md text-sm"
          />
          <Search className="absolute left-3 top-2.5 h-4 w-4 text-gray-400" />
        </div>

        {/* FILTERS */}
        <div className="flex gap-2">
          <select
            value={statusFilter}
            onChange={(e) => setStatusFilter(e.target.value)}
            className="border border-gray-400 bg-gray-200 text-sm rounded-md px-3 py-2 cursor-pointer"
          >
            <option value="">Status</option>
            <option value="approved">Approved</option>
            <option value="pending">Pending</option>
            <option value="reject">Rejected</option>
          </select>

          <select
            value={genderFilter}
            onChange={(e) => setGenderFilter(e.target.value)}
            className="border border-gray-400 bg-gray-200 text-sm rounded-md px-3 py-2 cursor-pointer"
          >
            <option value="">Gender</option>
            <option value="Male">Male</option>
            <option value="Female">Female</option>
          </select>
        </div>

      </div>

      {/* LOADING */}
      {loading && (
        <p className="flex justify-center">
          <Image src="/loading2.gif" width={90} height={90} alt="loading" />
        </p>
      )}

      {/* ERROR */}
      {error && <p className="text-center text-red-600 py-4">{error}</p>}

      {/* NO DATA */}
      {!loading && !error && users.length === 0 && (
        <p className="text-center text-gray-500 py-4">No users found.</p>
      )}

      {/* TABLE */}
      {!loading && !error && users.length > 0 && (
        <table className="min-w-full text-sm border border-gray-300">
          <thead className="bg-gray-50 text-gray-700 font-medium">
            <tr>

              <th className="p-2 border cursor-pointer" onClick={() => handleSort('name')}>
                <div className="flex items-center">
                  User Name <SortIcon field="name" />
                </div>
              </th>

              <th className="p-2 border cursor-pointer" onClick={() => handleSort('location')}>
                <div className="flex items-center">
                  Location <SortIcon field="location" />
                </div>
              </th>

              <th className="p-2 border cursor-pointer" onClick={() => handleSort('createdAt')}>
                <div className="flex items-center">
                  Joined <SortIcon field="createdAt" />
                </div>
              </th>

              <th className="p-2 border text-center cursor-pointer" onClick={() => handleSort('verified')}>
                <div className="flex justify-center items-center">
                  Verified <SortIcon field="verified" />
                </div>
              </th>

              <th className="p-2 border text-center cursor-pointer" onClick={() => handleSort('lastActive')}>
                <div className="flex justify-center items-center">
                  Last Active <SortIcon field="lastActive" />
                </div>
              </th>

              <th className="p-2 border text-center cursor-pointer" onClick={() => handleSort('status')}>
                <div className="flex justify-center items-center">
                  Status <SortIcon field="status" />
                </div>
              </th>

              <th className="p-2 border text-center">Actions</th>

            </tr>
          </thead>

          <tbody>
            {users.map((user, idx) => (
              <tr key={idx} className="text-gray-700">

                {/* NAME + IMAGE */}
                <td className="p-2 border">
                  <div className="flex items-center gap-3">
                    <img
                      src={user.profileImage || '/avatars/1.jpg'}
                      className="w-10 h-10 rounded-full object-cover"
                    />
                    <div className="flex flex-col">
                      <span className="font-medium">
                        {user.firstName} {user.lastName}
                      </span>
                      <span className="text-xs text-gray-500">
                        #{user._id?.slice(-5)} / {user.gender}
                      </span>
                    </div>
                  </div>
                </td>

                <td className="p-2 border">{user.city || 'Not Mention'}</td>

                <td className="p-2 border">
                  {new Date(user.createdAt).toLocaleDateString()}
                </td>

                <td className="p-2 border text-center">
                  {user.isMobileVerified ? (
                    <span className="text-green-600 font-semibold flex justify-center gap-2">
                      <span className="w-5 h-5 bg-green-600 text-white flex items-center justify-center rounded-sm">✔</span>
                      Yes
                    </span>
                  ) : (
                    <span className="text-red-600 font-semibold">✘ No</span>
                  )}
                </td>

                <td className="p-2 border text-center">
                  {new Date(user.updatedAt || user.createdAt).toLocaleDateString()}
                </td>

                <td className="p-2 border text-center">
                  <div className="inline-flex items-center gap-2">
                    <span
                      className={`w-3 h-3 rounded-full ${
                        user.adminApprovel === 'approved'
                          ? 'bg-green-600'
                          : user.adminApprovel === 'pending'
                          ? 'bg-yellow-500'
                          : 'bg-red-600'
                      }`}
                    ></span>
                    <span className="font-medium">
                      {user.adminApprovel === 'approved'
                        ? 'Approved'
                        : user.adminApprovel === 'pending'
                        ? 'Pending'
                        : 'Rejected'}
                    </span>
                  </div>
                </td>

                <td className="p-2 border text-center">
                  <Link href={`/manageusers/${user._id}`}>
                    <span className="text-black hover:underline cursor-pointer">
                      View
                    </span>
                  </Link>
                </td>

              </tr>
            ))}
          </tbody>
        </table>
      )}

      {/* PAGINATION EXACT SCREENSHOT STYLE */}
      {!loading && !error && totalPages > 1 && (
        <div className="flex justify-center items-center gap-2 mt-4 text-sm font-medium text-gray-700">

          <button
            onClick={() => goToPage(currentPage - 1)}
            disabled={currentPage === 1}
            className={`px-2 ${
              currentPage === 1 ? 'text-gray-400 cursor-default' : 'cursor-pointer'
            }`}
          >
            Prev
          </button>

          {Array.from({ length: Math.min(totalPages, 4) }, (_, i) => (
            <button
              key={i}
              onClick={() => goToPage(i + 1)}
              className={`px-1 ${
                currentPage === i + 1
                  ? 'font-semibold underline text-black'
                  : 'text-gray-700'
              }`}
            >
              {i + 1}
            </button>
          ))}

          {totalPages > 4 && <span>…..</span>}

          <button
            onClick={() => goToPage(currentPage + 1)}
            disabled={currentPage === totalPages}
            className={`px-2 ${
              currentPage === totalPages
                ? 'text-gray-400 cursor-default'
                : 'cursor-pointer'
            }`}
          >
            Next
          </button>

        </div>
      )}
    </div>
  );
}
